@mixin truncate {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.cursor-p {
    @media(min-width: 1200px) {
        cursor: pointer;
    }
}

.white-space-nowrap {
    white-space: nowrap;
}

